import * as moment from "moment";
import { Map } from "@ay/util";

export type TableSortFn = (a: any, b: any) => number;
export const TableSortFn = null;

export type TableSort = TableSortFn | "string" | "number" | "timestamp" | "moment";

export const TableSort: Map<TableSortFn> = {
  number: (a: number, b: number) => a - b,
  timestamp: (a: number, b: number) => a - b,
  moment: (a: moment.Moment, b: moment.Moment) => +a - +b,
  string: (a: string, b: string) => a > b ? 1 : a == b ? 0 : -1,
}
